import React, {useCallback, useEffect, useMemo, useRef, useState} from "react";
import ReactQuill, {Quill} from "react-quill";
import "react-quill/dist/quill.snow.css";
import axios from "axios";
import {useLocation, useNavigate} from "react-router-dom";
import ImageResize from "quill-image-resize/src/ImageResize";
Quill.register("modules/imageResize", ImageResize);

//      handler
function imageHandler() {
    const input = document.createElement("input");
    input.setAttribute("type", "file");
    input.setAttribute("accept", "image/*");
    input.click();

    input.onchange = () => {
        const file = input.files[0];
        // console.log("  image :", file);

        const formData = new FormData();
        formData.append("image", file);
        // console.log("Server    image : ", formData);

        const res =
            axios.post("/upload/image/register", formData, {
                headers: {
                    "Content-Type": "multipart/form-data",
                },
            })
            .then((response) => {
                // console.log("Server   Image URL :", response.data);
                const range = this.quill.getSelection();
                this.quill.insertEmbed(range.index, "image", `http://localhost:3000${response.data.imageUrl}`);
                // Front       
                // ex) http://localhost:3000/freeBoard/12.png    Editor       
            })
            .catch((error) => {
                console.error("Error uploading image:", error.response.data);
            });
    };
}

const Editor = ({boardNo, addBtn, updateBtn}) => {
    const quillRef = useRef(null);

    const modules = useMemo(() => ({
        toolbar: {
            container: [
                [{ 'font': [] }],
                [{ 'header': [1, 2, false] }],
                ['bold', 'italic', 'underline'],
                [{ 'color': [] }, { 'background': [] }],
                [{ 'script': 'sub' }, { 'script': 'super' }],
                [{ 'list': 'ordered' }, { 'list': 'bullet' }],
                [{ 'indent': '-1' }, { 'indent': '+1' }],
                [{ 'direction': 'rtl' }],
                ['link', 'image'],
                ['clean']
            ],
            handlers: {
                image: imageHandler
            }
        },
        imageResize: {
            parchment: Quill.import('parchment'),
            modules: ['Resize', 'DisplaySize', 'Toolbar']
        }
    }), []);

    // <Link>  boardNo  freeBoardNo, <Quill>   boardNo  
    // URL   parameter   Component  parameter    
    //  Editor          .()
    const [freeBoardNo, setFreeBoardNo] = useState(0); //  page  URL   freeBoardNo
    const [title, setTitle] = useState("");       // 
    const [content, setContent] = useState("");   // ( + )
    const titleRef = useRef(null);
    const navigate = useNavigate();
    const locate = useLocation();

    //  handler
    const titleOnChangeHandler = useCallback((e)=> {
        setTitle(e.target.value);
    });

    // TODO
    // React-Quill      , 

    //        
    const getFreeBoardContents = async () => {
        const res = await axios.get("/freeBoard/view", {
            params: {
                freeBoardNo: freeBoardNo
            }
        })
            .then((response => showContents(response)))
            .catch((error) => console.log("error occurred!"));
    }

    const showContents = (dbDatas) => {
        if (dbDatas) {
            setTitle(dbDatas.data.freeTitle);
            setContent(dbDatas.data.freeContent);
        }
    }

    //  button   DB    ,   
    useEffect(() => {
        console.log("  : ",freeBoardNo);
        if(!addBtn){            // URL   Editor comp       
            setFreeBoardNo(locate.state.freeBoardNo || boardNo);
        }
    }, []);

    useEffect(() => {
        console.log(" : ", freeBoardNo);
        if(freeBoardNo){
            getFreeBoardContents();
        }
    }, [freeBoardNo])

    // TODO
    // 1.         
    // 2.      

    //    
    const goAdd = async () => {
        if(title){
            if(content){
                const memberId = "21"; //       
                const data = {freeTitle : title, freeContent : content, memberId:memberId}
                const res = await axios.post("/freeBoard/register", data)
                .then((response => navigate("/community/freeBoard/view", {
                    state:response.data
                })))
                    .catch((error) => console.log(error));
            }else{
                alert(" ");
            }
        }else{
            alert(" ");
            titleRef.current.focus();
        }
    }

    //   
    const goUpdate = async () => {
        if (title) {
            if (content) {
                const memberId = "21"; //       
                const res = await axios.post("/freeBoard/update", null, {
                    params: {
                        freeBoardNo: freeBoardNo,
                        freeTitle: title,
                        freeContent: content,
                        memberId: memberId,
                    }
                })
                    .then((response => navigate("/community/freeBoard/view", {
                        state: response.data
                    })))
                    .catch((error) => console.log(error));
            } else {
                alert(" ");
                quillRef.focus();
            }
        } else {
            alert(" ");
            titleRef.current.focus();
        }
    }

    const goCancle = () => {
        if (window.confirm("  ?")) {
            navigate(-1)
        }
    }

    return (
        <div>
            <input type="text" ref={titleRef} onChange={titleOnChangeHandler} value={title}
                   className="text-black text-4xl w-full" placeholder=" "/>
            <hr className="my-3" />
            <ReactQuill
                ref={quillRef}
                value={content}
                onChange={setContent}
                modules={modules}
                theme="snow"
                style={{
                    height:'500px',
                    color:'#000000',
                    overflowY:'visible',
                }}
            />
            <div className="text-end mt-14">
                {updateBtn ?
                    <>
                        <button onClick={goCancle}
                                className="bg-red-500 text-100 p-2 font-bold text-white rounded hover:bg-blue-700 me-5">
                        </button>
                        <button onClick={goUpdate}
                                className="bg-blue-500 text-100 p-2 font-bold text-white rounded hover:bg-blue-700">
                            
                        </button>
                    </>
                    :
                    <>
                        <button onClick={goCancle}
                                className="bg-red-500 text-100 p-2 font-bold text-white rounded hover:bg-blue-700 me-5">
                        </button>
                        <button onClick={goAdd}
                                className="bg-blue-500 text-100 p-2 font-bold text-white rounded hover:bg-blue-700">
                            
                        </button>
                    </>
                }
            </div>
        </div>
    );
};

export default Editor;
